import React, {useState} from "react";
import {
  phone,
  email,
  address,
  calendar,
  UI,
  apps,
  imageOpt,
  web,
  photograhpy,
  webDev,
  logo,
  about,
  resume,
  work,
  blog,
  contact,
  aboutHovering,
  resumeHovering,
  workHovering,
  blogHovering,
  contactHovering,
} from "./images/index";
import Header from "./Header";
const About = () => {
  const [hoveredImage, setHoveredImage] = useState(null);
  const [clickedImage, setClickedImage] = useState(about);

  const handleMouseEnter = (image) => {
    setHoveredImage(image);
    setClickedImage(null);
  };

  const handleMouseLeave = () => {
    setHoveredImage(null);
  };

  const handleClick = (image) => {
    setClickedImage(image);
  };

  const infoData = [
    {img: phone, label: "Phone", value: "+923439228702"},
    {
      img: address,
      label: "Location",
      value: "Khyaban e ameen society G block, Lahore.",
    },
    {img: email, label: "Email", value: "ashrafapprocx@gmail.com"},
    {img: calendar, label: "Birthday", value: "April 07"},
  ];
  const backgroundColors = [
    null,
    "#FEFAF0",
    null,
    "#FFF4F4",
    "#FFF0F8",
    "#F3FAFF",
  ];
  const [isSkill, setIsSkill] = useState([
    {
      image: UI,
      sikllHeader: "Ui/Ux Design",
      description:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam euismod volutpat.",
    },
    {
      image: apps,
      sikllHeader: "App Development",
      description:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam euismod volutpat.",
    },
    {
      image: imageOpt,
      sikllHeader: "Image Gallary",
      description:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam euismod volutpat.",
    },
    {
      image: photograhpy,
      sikllHeader: "Photography",
      description:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam euismod volutpat.",
    },
    {
      image: web,
      sikllHeader: "Web Development",
      description:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam euismod volutpat.",
    },
    {
      image: webDev,
      sikllHeader: "Web Development",
      description:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam euismod volutpat.",
    },
  ]);
  return (
    <div>
      <div>
        <div className="header_p">
          <div className="logo_parent">
            <img src={logo} alt="logo" className="logo_name" />
          </div>
          <div className="header_parent">
            <ul className="header_tab">
              <li>
                <img
                  src={
                    hoveredImage === about || clickedImage === about
                      ? aboutHovering
                      : about
                  }
                  alt="about"
                  onMouseEnter={() => handleMouseEnter(about)}
                  onMouseLeave={handleMouseLeave}
                  onClick={() => handleClick(about)}
                  className="svg-logo"
                />
              </li>
              <li>
                <img
                  src={
                    hoveredImage === resume || clickedImage === resume
                      ? resumeHovering
                      : resume
                  }
                  alt="resume"
                  onMouseEnter={() => handleMouseEnter(resume)}
                  onMouseLeave={handleMouseLeave}
                  onClick={() => handleClick(resume)}
                  className="svg-logo"
                />
              </li>
              <li>
                <img
                  src={
                    hoveredImage === work || clickedImage === work
                      ? workHovering
                      : work
                  }
                  alt="work"
                  onMouseEnter={() => handleMouseEnter(work)}
                  onMouseLeave={handleMouseLeave}
                  onClick={() => handleClick(work)}
                  className="svg-logo"
                />
              </li>
              <li>
                <img
                  src={
                    hoveredImage === blog || clickedImage === blog
                      ? blogHovering
                      : blog
                  }
                  alt="blog"
                  onMouseEnter={() => handleMouseEnter(blog)}
                  onMouseLeave={handleMouseLeave}
                  onClick={() => handleClick(blog)}
                  className="svg-logo"
                />
              </li>
              <li>
                <img
                  src={
                    hoveredImage === contact || clickedImage === contact
                      ? contactHovering
                      : contact
                  }
                  alt="contact"
                  onMouseEnter={() => handleMouseEnter(contact)}
                  onMouseLeave={handleMouseLeave}
                  onClick={() => handleClick(contact)}
                  className="svg-logo"
                />
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="about_parent">
        <div className="about_first">
          <div className="about_me">
            <span>About Me</span> <div className="about_red"></div>
          </div>
          <div className="Intro_parent">
            <div className="empty"></div>
            <div className="intro_child">
              <div>
                <div className="header_info who_am">Who am i?</div>
                <p className="phragraph">
                  I'm Creative Director and UI/UX Designer from Sydney,
                  Australia, working in web development and print media. I enjoy
                  turning complex problems into simple, beautiful and intuitive
                  designs.
                </p>
                <p className="phragraph">
                  I'm Creative Director and UI/UX Designer from Sydney,
                  Australia, working in web development and print media. I enjoy
                  turning complex problems into simple, beautiful and intuitive
                  designs.
                </p>
              </div>
              <div className="parent_info">
                <div className="header_info personal_info">Personal Info</div>
                <div className="info_detail">
                  {infoData.map((item, index) => (
                    <div key={index} className="info_child">
                      <div className="info_imag_parent">
                        <img src={item.img} alt="" />
                      </div>
                      <div>
                        <div>{item.label}</div>
                        <div>{item.value}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
          <div className="experinece_name ">
            <div className="header_info ">What I do!</div>
            <div className="info_detail experince_child">
              {isSkill.map((skill, index) => (
                <div
                  key={index}
                  className="skils"
                  style={{
                    backgroundColor: backgroundColors[index],
                  }}>
                  <div className="">
                    <img src={skill.image} alt="" />
                  </div>
                  <div className="">
                    <div className="skill_header">{skill.sikllHeader}</div>
                    <p style={{textAlign: "justify"}}>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          {/* <div className="skills">App Development</div>
        <div className="org">Organization</div> */}
        </div>
      </div>
    </div>
  );
};

export default About;
